import './Project.css';
import React, { useState, useEffect } from 'react';
import { imgdata, imgdata2, imgdata3, imgdata4, imgdata5 } from './imgdata'
import ImageSliderModal from './ImageSliderModal';
import { Container, Row, Col } from 'react-bootstrap'

function Project() {
    const breakpoints = [768, 1024, 1366];
    useEffect(()=>{
      const handleResize = () => {
        const width = window.innerWidth;
        
        if(breakpoints.includes(width)) {
          window.location.reload();
        }
      };
      window.addEventListener("resize", handleResize);
      return () =>{
        window.removeEventListener("resize", handleResize);
      };
    }, []);

    return (
        <>
            <div className='Project'>
            
                <span className='Project_1'>
                    <br/>
                    완성된 공간의 변화를 직접 확인해 보세요!
                </span>
                <span className='Project_1'>
                    시공 전후의 놀라운 변신을 경험해보세요
                </span>
            </div>
            <br />
            <div className='project_container' >
                <p>FB bath 거실/주방 공사</p>
                <div className='project_imgbox'>
                    <ImageList style={{gap:'1px', display: 'flex' }} 
                    imgdata={imgdata} count={window.innerWidth <= 768 ? 3 : window.innerWidth <= 1366 ? 4 : 5} startIndex={4} />
                </div>

            </div>
            <div className='project_container'>
                <p>FB bath 화장실/욕실 공사</p>
                <div className='project_imgbox2'>
                    <div className='bath_part'>
                        <p> 다크 그레이 </p>
                        <ImageList imgdata={imgdata2} count={1} startIndex={0} />
                    
                    </div>
                    <div className='bath_part'>
                        <p> 화이트 그레이</p>
                        <ImageList imgdata={imgdata3} count={1} startIndex={0} />
                        
                    </div>
                    <div className='bath_part'>
                        <p> 우드</p>
                        <ImageList imgdata={imgdata4} count={1} startIndex={0} />
                        
                    </div>
                </div>
            </div>
        </>
    )
}
// 아이템 뷰
// ImageCard 컴포넌트 정의
function ImageCard({ imgdata }) {
    return (
        <div className="ImageCard"
        >
            <img
                src={imgdata.image}
                alt={`Image 1 of ${imgdata.id}`}
                style={{
                    width: '100%', height: window.innerWidth <= 768 ? '100%' : '400px', objectFit: 'cover'
                }}
            />

        </div>
    );
}



function ImageList({ imgdata, count, startIndex = 0, style }) {
    const displayedImages = imgdata.slice(startIndex, startIndex + count);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [currentImageIndex, setCurrentImageIndex] = useState(0);

    const openModal = (index) => {
        setCurrentImageIndex(index + startIndex);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
    };

    return (
        <div className="ImageList" style={style}>
            {displayedImages.map((data, index) => (
                <div key={data.id} onClick={() => openModal(index)}>
                    <ImageCard imgdata={data}
                    />
                </div>
            ))}
            {isModalOpen && (
                <ImageSliderModal
                    images={imgdata}
                    currentIndex={currentImageIndex}
                    onClose={closeModal}
                />
            )}
        </div>
    );
}
export default Project;